// This is Header component /Navigation Component
import logo from "../../assets/logo.svg";
import styled from "styled-components";
import React, { useState } from "react";

const Headers = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--nav);
  color: var(--white);
  padding: 1rem 3rem;
  position: relative;
  z-index: 500;
`;

const Logo = styled.a`
  display: flex;
  align-items: center;
  width: 2rem;
  height: auto;
  cursor: pointer;

  img {
    margin-right: 0.5rem;
  }
`;

const Nav = styled.nav`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 25rem;
  max-width: 40rem;

  @media only screen and (max-width: 48em) {
    display: none;
  }
  a {
    font-weight: 600;
    line-height: 1.5;
    color: var(--white);

    &::before {
      content: "";
      display: block;
      width: 3px;
      height: 3px;
      background: transparent;
      transition: width 0.5s;
    }
    &:not(:last-child):hover::before {
      width: 100%;
      background: #00bffe;
    }
  }
`;
const Button = styled.button`
  padding: 1rem 2rem;
  border-radius: 25px;
  background-color: var(--purple);
  transition: all 0.5s;
  color: var(--white);
  cursor: pointer;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 100px;
    transition: all 0.4s;
    z-index: -1;
  }

  &:hover:before {
    transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
  }
`;

const Hamberger = styled.button`
  position: relative;
  background-color: transparent;
  width: 2rem;
  height: 2px;
  cursor: pointer;
  display: none;

  @media only screen and (max-width: 48em) {
    display: inline-block;
  }
  span {
    background-color: var(--white);
    width: 2rem;
    height: 2px;
    display: ${(props) => (props.click ? "none" : "inline-block")};
    position: absolute;
    left: 0;
    cursor: pointer;
    transition: all 0.3s;
  }

  &::before,
  &::after {
    content: "";
    background-color: var(--white);
    width: 2rem;
    height: 2px;
    display: inline-block;
    position: absolute;
    left: 0;
    cursor: pointer;
    transition: all 0.3s;
  }
  &::before {
    top: ${(props) => (props.click ? "0" : "-0.3rem")};
    transform: ${(props) => (props.click ? "rotate(135deg)" : "rotate(0deg)")};
  }
  &::after {
    top: ${(props) => (props.click ? "0" : "0.5rem")};
    transform: ${(props) => (props.click ? "rotate(-135deg)" : "rotate(0deg)")};
  }
`;

const MobileMenu = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 100%;
  left: 0;
  padding: 2rem 0;
  background-color: rgba(53, 53, 63, 0.95);
  width: 96%;
  margin: 0.5rem;
  border-radius: 20px;
  visibility: ${(props) => (props.click ? "visible" : "hidden")};
  opacity: ${(props) => (props.click ? "1" : "0")};
  overflow-x: hidden;
  a {
    margin-top: 1.5rem;
    font-weight: 500;
    color: var(--white);
  }
  button {
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: white;
      border-radius: 100px;
      transition: all 0.4s;
      z-index: -1;
    }

    &:hover:before {
      transform: scaleX(1.4) scaleY(1.6);
      opacity: 0;
    }

    &:hover {
      transform: translateY(-3px);
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    }
  }
`;

const Header = () => {
  const [open, setOpen] = useState(false);
  const handleClick = () => setOpen(!open);
  return (
    <Headers>
      <Logo>
        <img src={logo} />
        <h4>LogoMain</h4>
      </Logo>
      <Nav>
        <a href="#home">صفحه اصلی</a>
        <a href="#about">درباره ما</a>
        <a href="#services">محصولات آموزشی</a>
        <a href="#contact">
          <Button>تماس با ما</Button>
        </a>
      </Nav>
      <Hamberger onClick={handleClick} click={open}>
        <span />
      </Hamberger>
      <MobileMenu click={open}>
        <a href="#home" onClick={handleClick}>
          صفحه اصلی
        </a>
        <a href="#about" onClick={handleClick}>
          درباره ما
        </a>
        <a href="#services" onClick={handleClick}>
          خدمات
        </a>
        <a href="#contact" onClick={handleClick}>
          <Button>تماس با ما</Button>
        </a>
      </MobileMenu>
    </Headers>
  );
};

export default Header;
